import React from 'react';
import KosningaProf from '../../questionnaire-results/KosningaProf';
import questions from '../../../../data/poll/questions.json';
import answers from '../../../../data/poll/answers.json';
import Layout from '../../../components/Layout';

export default ({ url }) => ({
  chunks: ['embed-questionnaire'],
  title: `Vpraalnik Voli prav`,
  description:
    'Hvaa flokkar og frambjendur eru r algjrlega sammla?  getur reytt prfi og komist a reianlegri niurstu.',
  path: url,
  ogImage: 'https://assets.voliprav.si/og_prof.png',
  component: (
    <Layout isEmbed>
      <KosningaProf
        isEmbedded
        title="Vpraalnik Voli prav"
        answers={answers}
        questions={questions}
      />
    </Layout>
  ),
});



